/************************

商品详情页底部导航栏

************************/

.product-nav-bar {
	// position: fixed;
	// z-index: @z-index-nav-search-bar;
	// width: 100%;
	// max-width: @max-width;
	// height: unit(@product-nav-bar-height/@base-px-2-rem, rem);
	// background-color: #fff;
	.make-fixed-bar(@product-nav-bar-height);
	bottom: 0;
	//border-top: 1px solid @bar-border-color;
	//box-shadow: -1px 0 3px 0 @border-color-gray-light;
	.fixed-shadow(top, 2px);
	display: flex;
	flex-flow: row nowrap;
	justify-content: space-between;
	align-items: stretch;
	.product-action {
		width: 45%;
		height: 100%;
	}
	.product-action-buy {
		width: 55%;
	}
}

ul.product-nav-action {
	display: flex;
	flex-flow: row nowrap;
	justify-content: space-around;
	align-items: stretch;
	height: 100%;

	//三个icon
	li {
		font-size: 0;
		a {
			text-align: center;
			display: block;
			&.active {
				color: red;
			}

		}
		//icon 下的文字
		p {
			//test value line-height
			line-height: unit(45/@base-px-2-rem, rem);
			font-size: 12px;
			color: @product-nav-text-color;
			.mod-retina-display(20px, 0px);
		}
	}
	//显示购物车 数量角标
	li {
		&:nth-child(2) a i {

			&[data-shop-count="0"] {
				&::before {
					display: none;
				}
			}

			position: relative;
			&::before {
				//购物车商品数量
				-webkit-text-size-adjust: none;
				//http://www.jb51.net/article/81984.htm
				//dataset
				position: absolute;
				display: block;
				content: attr(data-shop-count);
				font-style: normal;

				//z-index: @z-index-nav-search-bar + 1;
				right: calc(~"-18% - 1px");
				top: -1px;
				height: 16px;
				//修正字体的右偏移
				width: 15px;
				font-size: 12px;
				line-height: 1.4;
				transform: scale(0.85);
				letter-spacing: -1px;
				vertical-align: top;
				//修正字体的右偏移
				padding: 0 1px 0 0;

				color: #fff;
				background-color: @red;
				box-shadow: 0 0 0px 1px rgba(255, 255, 255, 1);
				border-radius: 50%;
				//修正ip6 偏移量
				@media screen and (min-width: 320px) {
					transform: scale(0.85);
				}
				//修正6p 偏移量
				@media screen and (min-width: 414px) {
					transform: scale(0.95);
					top: 1px;
				}

			}
		}
	}
}

//加入购物车 与 拼团
ul.product-nav-buy {
	display: flex;
	flex-flow: row nowrap;
	justify-content: space-around;
	align-items: center;
	//加入购物车
	//拼团
	height: 100%;
	li {
		text-align: center;
		width: 100%;
		height: unit((@product-nav-bar-height) /@base-px-2-rem, rem);
		//加入购物车 团购内文字
		a {
			display: flex;
			flex-flow: column nowrap;
			justify-content: center;
			align-items: stretch;
			height: 100%;
			p {
				//兼容团购的两行文字 一半高度
				line-height: unit((@product-nav-bar-height/2) /@base-px-2-rem, rem);
				height: unit((@product-nav-bar-height/2) /@base-px-2-rem, rem);
				width: 100%;
				color: #fff;
				font-size: 17px;
				letter-spacing: 1px;
				.mod-retina-display(20px, 2px);
				//修复小屏幕 字体过大
				@media screen and (max-width: 320px) {
					font-size: 14px;
					letter-spacing: 0px;
				}
			}
		}
		&:first-child {
			//width: 55%;
			background-color: @group-pink;
		}
		&:last-child {
			//width: 55%;
			background-color: @red;
		}
	}
}

//商品详情页显示购物车数量
li[data-shop-count="0"] {
	&::before {
		display: none !important;
	}
}

/****************

购物车结算栏

****************/

.shopcar-nav-bar {
	.make-fixed-bar(@nav-bottom-bar-height);
	//位于base nav上
	bottom: unit(@nav-bottom-bar-height/@base-px-2-rem, rem);
	//border-top: 1px solid @bar-border-color;
	.fixed-shadow(top, 2px);
	padding-left: unit(@shopcar-left-right-blank/@base-px-2-rem, rem);
	display: flex;
	flex-flow: row nowrap;
	justify-content: space-between;
	align-items: center;
	.shopcar-check-icon {
		margin-right: 0;
	}
	.shopcar-check-icon + span {
		text-align: left;
		width: 20%;
		color: #333;
		font-size: 18px;
		font-weight: 500;
	}
	.order-price {
		width: 34%;
		//height: 100%;
		text-align: right;
		//padding-right: unit( (@shopcar-left-right-blank - 20)/@base-px-2-rem, rem);
		p {
			font-size: 15px;
			&:first-child {
				color: @red;
			}
			&:nth-child(2) {
				color: @text-gray;
				font-size: 13px;
			}
		}
	}
	a.order-btn {
		width: 33.33%;
		display: block;
		//通用
		.base-nav-bottom-bar-big-btn();
	}
}

/****************

底部导航满高按钮

****************/

.base-nav-bottom-bar-big-btn() {
	background-color: @red;
	height: 100%;
	text-align: center;
	line-height: unit(@nav-bottom-bar-height/@base-px-2-rem, rem);
	font-size: 16px;
	font-weight: 500;
	letter-spacing: 2px;
	color: #fff;
}

/****************

购物车下单-订单信息

****************/

.order-comfirm-bar {
	display: flex;
	flex-flow: row nowrap;
	justify-content: space-around;
	align-items: center;
	height: 100%;
	width: 100%;
	font-size: 15px;
	.order-comfirm-price, .order-comfirm-btn {
		height: 100%;
		align-self: center;
	}
	.order-comfirm-price {
		width: 60%;
		height: 100%;
		text-align: center;
		line-height: unit(@nav-bottom-bar-height/@base-px-2-rem, rem);
		padding-right: unit(200/@base-px-2-rem, rem);
		//color: @red;
	}
	.order-comfirm-btn {
		width: 40%;
		height: 100%;
		text-align: center;
		line-height: unit(@nav-bottom-bar-height/@base-px-2-rem, rem);
		.base-nav-bottom-bar-big-btn();
	}
}

/****************

就手收到订单

****************/

#get-order {
	.make-fixed-bar(@nav-bottom-bar-height);
	padding: unit(10/@base-px-2-rem, rem);
	top: unit((@search-bar-height+50)/@base-px-2-rem, rem);
	left: unit(80/@base-px-2-rem, rem);
	width: auto;
	//width: unit( 850/@base-px-2-rem, rem);
	height: unit(120/@base-px-2-rem, rem);
	background-color: rgba(0, 0, 0, 0.35);
	font-size: 12px;
	.make-border-radius(20px);
	display: flex;
	flex-flow: row nowrap;
	justify-content: flex-start;
	align-items: center;
	//display: none;
	opacity: 0;
	.get-order-imgBox {
		width: unit(100/@base-px-2-rem, rem);
		height: unit(100/@base-px-2-rem, rem);
		img {
			width: 100%;
			height: 100%;
			.make-border-radius(20px);
		}
	}
	p {
		color: #fff;
		padding: 0 unit(20/@base-px-2-rem, rem);
	}
	//修复小屏幕显示
	@media screen and (max-width: 320px) {
		//width: unit( 980/@base-px-2-rem, rem);
		width: auto;
	}
}

/****************

加入购物车

****************/

//旋转的图
#TurnAround-img {
	.make-fixed-bar(@nav-bottom-bar-height);
	height: unit(150/@base-px-2-rem, rem);
	width: unit(150/@base-px-2-rem, rem);
	//修正默认位置与返回按钮重叠
	left: unit(150/@base-px-2-rem, rem);
	.make-border-radius(30px);
}

/****************

评论页 点击放大评论图

****************/

.full-screen {
	position: fixed;
	z-index: @z-index-nav-search-bar + 1;
	width: 100%;
	max-width: @max-width;
	height: 100%;
	height: 100vh;
	background-color: rgba(0, 0, 0, .55);
	flex-flow: column nowrap;
	justify-content: center;
	align-items: stretch;
	display: none;
	.full-screen-imgBox {
		position: relative;
		img {
			width: 100%;
		}
	}
	&:after {
		display: block;
		content: "";
		position: absolute;
		top: 10%;
		right: 1%;
		height: unit(100/@base-px-2-rem, rem);
		width: unit(100/@base-px-2-rem, rem);
		background-color: rgba(255, 255, 255, .1);
		background-image: url("@{icon-address}/x.png");
		background-repeat: no-repeat;
		//background-origin: content-box;
		background-size: 120% 120%;
		background-position: center center;
		.make-border-radius(50px);
	}
}

